import react from 'react'
import classes from './Post.module.css'

const Post = (props) => {
    return (
    <div className={classes.item} >
        <div>
        <img src='data:image/jpeg;base64,'/>
        { props.message }
       </div> 
       <span> Like </span> { props.likesCount } 
    </div>
    
    )
}

export default Post